.card_header {
  width: 640px;
  height: 233.2px;
  background: url("../../../assets/images/college_02.jpg") no-repeat center;
  box-shadow: inset 0 0 0 2000px rgba(0, 0, 0, 0.71);
  display: flex;
  background-size: cover;
  flex-direction: column;
  justify-content: space-between;
}

.header_right {
  display: flex;
  flex-direction: column;
  color: #ffffff;
  text-transform: uppercase;
}

.header_promoted {
  width: 152px;
  height: 33px;
  background-color: #68a7ff;
  transform: translate(1px, -16px);
  align-self: flex-end;
  font-size: 15px;
  clip-path: polygon(100% 0, 100% 50%, 100% 100%, 0% 100%, 10% 49%, 0% 0%);
}

.header_rating {
  display: flex;
  margin: 13px;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 75px;
  height: 53px;
  border-radius: 3.8px;
  background-color: #f5a626;
  align-self: flex-end;
  font-size: 13.5px;
  line-height: 1.33;
}

.header_bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #444444;
}

.header_tags {
  display: flex;
  padding: 10px;
  font-size: 13.5px;
  line-height: 1.33;
}

.tags_best,
.tags_distance {
  width: 135px;
  height: 34px;
  border-radius: 16.5px;
  background-color: #ffffff;
  margin-right: 10px;
  padding: 0px 10px;
}

.d-flex-text {
  display: flex;
  justify-content: center;
  align-items: center;
}

.header_number .number_text {
  font-size: 13px;
  line-height: 1.33;
  color: #ffffff;
  margin-right: 10px;
}
